import React, {useState} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import {ArrowLeftOutlined, SaveOutlined} from "@ant-design/icons";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Card, Form, message,} from "antd";
import Utils from "../../../../../../../utils";
import moment from "moment";
import PreloaderText from "../../../../../../../components/animated-components/preloader-text";
import ApiService from "../../../../../../../services/api.service";
import DisplaySystemForm from "../components/display-system.form";
import {SocketSend} from "../../../../../../../redux/actions/websocket.action";
import {ON_DISPLAY_SYS_PUSH} from "../../../../../../../constants/socket.event";
import {useHistory} from "react-router-dom";

function mapStateToProps(state) {
	return {};
}

function Index(props) {
	let {SocketSend} = props
	const history = useHistory()
	
	
	const [form] = Form.useForm();
	const [loading, setLoading] = useState(false);
	const onFinish = () => {
		setLoading(true)
		message.loading({
			key: "create-display-system",
			content: <PreloaderText text={'Loading'} dots={3} speed={300}/>,
			duration: 5
		})
		form.validateFields()
			.then((value) => {
				let fields = {}
				Object.entries(value).map(([key, value]) => {
					switch (key) {
						case 'status':
							Reflect.set(fields, key, `${value}`.toLowerCase())
							break
						case "date":
							Reflect.set(fields, key, moment(value).format("YYYY-MM-DD"))
							break;
						case "schedule_time":
						case "actual_time":
							if (value) {
								Reflect.set(fields, key, moment(value).format("HH:mm"))
							} else {
								Reflect.set(fields, key, null)
							}
							break;
						default:
							Reflect.set(fields, key, value)
					}
				})
				if (fields && typeof (fields) === 'object' && Object.keys(fields).length > 0) {
					new ApiService({
						url: `/api/v1/flight`,
						body: fields,
					}).post()
						.then((response) => {
							if (response?.success) {
								message.success({
									key: "create-display-system",
									content: response?.message ?? "Successfully! save to database",
									duration: 5
								})
								SocketSend(JSON.stringify({
									from: Utils.getUserId,
									to: fields?.type,
									room: fields?.type,
									event: ON_DISPLAY_SYS_PUSH,
									type: "update_event",
									message: ON_DISPLAY_SYS_PUSH,
									data: response?.data
								}))
								form.resetFields()
							} else {
								message.error({
									key: "create-display-system",
									content: response?.message ?? "Error: fail to database",
									duration: 5
								})
							}
						})
						.catch((err) => {
							message.error({
								key: "create-display-system",
								content: err?.message ?? "Error: fail to database",
								duration: 5
							})
						})
				}
				setLoading(false)
				
			})
			.catch((err) => {
				message.error({
					key: "create-display-system",
					content: err?.message ?? "Some Error Field",
					duration: 5
				})
			})
	}
	
	return (
		<>
			<HeroComponents
				loading={false}
				name={"Create"}
				positionName={['FIDS', "Display"]}
				thumbnail={null}
				positionColor={'cyan'}
				extra={<div className={'d-flex align-items-center mk-gap-2'}>
					<ButtonPermission
						permission={['admin']}
						theme={{
							type: "notification",
							property: {
								message: "Access Denied",
								description: "Anda tidak dapat menyimpan ke database "
							}
						}}
						config={{
							icon: <ArrowLeftOutlined/>,
							type: "primary",
							danger: false,
							ghost: true,
							size: "small",
							onClick: function () {
								history.push('/dashboard/display/system')
							}
						}}>
						<span>Back</span>
					</ButtonPermission>
					<ButtonPermission
						permission={['admin']}
						theme={{
							type: "notification",
							property: {
								message: "Access Denied",
								description: "Anda tidak dapat menyimpan ke database "
							}
						}}
						config={{
							icon: <SaveOutlined/>,
							type: "primary",
							danger: false,
							ghost: false,
							size: "small",
							onClick: function () {
								onFinish();
							}
						}}>
						<span>Save</span>
					</ButtonPermission>
				</div>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false}>
						<DisplaySystemForm
							form={form}
							initialValue={{}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</>
	);
}

export default connect(
	mapStateToProps, {SocketSend}
)(Index);